   .app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .content-wrapper {
    flex: 1;
  }
  
  .footer {
    background-color: #333;
    color: white;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1% 0 0 0;
  }
    
  .footer h4 {
    display: inline-block;
    margin-right: 10px;
  }
  
  .empty-space {
    height: 200px;
  }
